import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

import CorsaireImg from "../../assets/img/corsaires.png";
import SauveteurImg from "../../assets/img/sauveteurs.png";

import * as SC from "./styled";

const Header = () => {
  const [actual, setActual] = useState("/");
  const location = useLocation();
  let url = ''
  
  useEffect(() => {
    setActual(location.pathname.split("/")[1]);
  }, [location.pathname])

  if (process.env.REACT_APP_NODE_ENV === 'production') {
    if (location && location.pathname && !location.pathname.contains('NDI-2021-Front')) {
      url = 'NDI-2021-Front'
    } else {
      url = ''
    }
  }

  return (
    <SC.Container>
      <SC.Wrapper>
        <SC.Left>
          <SC.Logo src={CorsaireImg} />
          <SC.Logo src={SauveteurImg} />
          <SC.Title to={`${url}/`}>{"Les Sauveteurs du dunkerquois"}</SC.Title>
          {url}
        </SC.Left>
        <SC.Nav>
          <SC.NavItem>
            <SC.NavLink actual={actual === "search"} to={`${url}/search`}>{"Recherche"}</SC.NavLink>
          </SC.NavItem>
          <SC.NavItem>
            <SC.NavLink actual={actual === "article"} to={`${url}/article`}>{"Article"}</SC.NavLink>
          </SC.NavItem>
          <SC.NavItem>
            <SC.NavLink actual={actual === "community"} to={`${url}/community`}>{"Communauté"}</SC.NavLink>
          </SC.NavItem>
        </SC.Nav>
      </SC.Wrapper>
    </SC.Container>
  );
}

export default Header;